<template>
	<div class="case-box drag-list">
		<h1></h1>
		<p class="mt10">http://www.sortablejs.com/options.html </p>
		<p class="mt10">Sortable.js vue-draggable</p>
		<p class="mt10">http://www.itxst.com/vue-draggable/tutorial.html</p>
		<!--  -->
		<div class="scene"></div>
		<ul class="list-box list-box1" id="list1">
			<li class="item" v-for="(item, index) in list" :key="index">
				<span v-text="`${index+1}. `"></span>
				<span v-text="item.text"></span>
			</li>
		</ul>
		<p class="mt10">
			<span class="title">old:</span>
			<span v-text="oldList"></span>
		</p>
		<p class="mt10">
			<span class="title">new:</span>
			<span v-text="newList"></span>
		</p>

		<!--  -->
		<div class="scene"></div>
		<div class="lisg-group">
			<ul class="list-box list-box2" id="list2">
				<li class="item" v-for="(item, index) in list2" :key="index">
					<span v-text="`${item.id}. `"></span>
					<span v-text="item.text"></span>
				</li>
			</ul>
			<ul class="list-box list-box3" id="list3">
				<li class="item" v-for="(item, index) in list3" :key="index">
					<span v-text="`${item.id}. `"></span>
					<span v-text="item.text"></span>
				</li>
			</ul>
		</div>
		
	</div>
</template>

<script>
import Sortable from 'sortablejs'


// Vue.use(Sortable)

export default {
	name: 'drag-list',
	data() {
		return {
			list: [
				{
					id: 1,
					text: ''
				},
				{
					id: 2,
					text: ''
				},
				{
					id: 3,
					text: ''
				},
				{
					id: 4,
					text: ''
				},
				{
					id: 5,
					text: ''
				},
				{
					id: 6,
					text: ''
				}
			],
			list2: [
				{
					id: 1,
					text: ''
				},
				{
					id: 2,
					text: ''
				},
				{
					id: 3,
					text: ''
				},
				{
					id: 4,
					text: ''
				},
				{
					id: 5,
					text: ''
				},
				{
					id: 6,
					text: ''
				}
			],
			list3: [
				{
					id: 7,
					text: ''
				},
				{
					id: 8,
					text: ''
				},
				{
					id: 9,
					text: ''
				},
				{
					id: 10,
					text: ''
				},
				{
					id: 11,
					text: ''
				},
				{
					id: 12,
					text: ''
				}
			],
			oldList: [],
			newList: [],
			newListLeft: [],
			newListRight: [],
		}
	},
	mounted () {
		this.getList()
	},
	methods: {
		getList () {
			this.oldList = this.list.map(v => v.id)
			this.newList = this.oldList.slice()
			
			this.oldList = this.list2.map(v => v.id)
			this.newListLeft = this.oldList.slice()
			
			this.$nextTick(() => {
        this.setSort()
      })
		},
		setSort () {
			let node1 = document.getElementById('list1')
			let node2 = document.getElementById('list2')
			let node3 = document.getElementById('list3')
			let sortable = new Sortable(node1, {
				animation: 150,
				ghostClass: 'sortable-current', // 
				draggable: ".item",  // 
				dragClass: "sortable-drag",  // css
				chosenClass: "sortable-chosen",  // css 
				direction: 'horizontal', //  ()
				forceFallback: false,  //  HTML5
				/**  **/
				// 
				onStart: function (evt) {
					console.log('start');
					console.log(evt);
				},
				// 
				onEnd: evt => {
					const targetRow = this.list.splice(evt.oldIndex, 1)[0]
          const tempIndex = this.newList.splice(evt.oldIndex, 1)[0]
          this.list.splice(evt.newIndex, 0, targetRow)
          this.newList.splice(evt.newIndex, 0, tempIndex)
				},
			})

			let sortable2 = new Sortable(node2, {
				group: 'shared',
				animation: 150,
				ghostClass: 'sortable-current', // 
				draggable: ".item",  // 
				dragClass: "sortable-drag",  // css
				chosenClass: "sortable-chosen",  // css 
				direction: 'horizontal', //  ()
				forceFallback: false,  //  HTML5
				/**  **/
				// 
				onStart: function (evt) {
					console.log('start');
					console.log(evt);
				},
				// 
				// onEnd: evt => {
				// 	console.log('2222222');
				// 	console.log(evt.to);
				// 	console.log(evt.from);
				// 	console.log(evt.newIndex);
				// 	const targetRow2 = this.list2.splice(evt.oldIndex, 1)[0]
				// 	const tempIndex2 = this.newListLeft.splice(evt.oldIndex, 1)[0]
				// 	console.log('222');
				// 	console.log(tempIndex2);
        //   this.list2.splice(evt.newIndex, 0, targetRow2)
        //   this.newListLeft.splice(evt.newIndex, 0, tempIndex2)
				// },
				onChange: evt => {
					console.log('onChange');
					console.log(evt.to);
						// evt.newIndex

				  const targetRow2 = this.list2.splice(evt.oldIndex, 1)[0]
					const tempIndex2 = this.newListLeft.splice(evt.oldIndex, 1)[0]
          this.list2.splice(evt.newIndex, 0, targetRow2)
          this.newListLeft.splice(evt.newIndex, 0, tempIndex2)
				}
			})

			let sortable3 = new Sortable(node3, {
				group: 'shared',
				animation: 150,
				ghostClass: 'sortable-current', // 
				draggable: ".item",  // 
				dragClass: "sortable-drag",  // css
				chosenClass: "sortable-chosen",  // css 
				direction: 'horizontal', //  ()
				forceFallback: false,  //  HTML5
				/**  **/
				// 
				// onStart: function (evt) {
				// 	console.log('start');
				// 	console.log(evt);
				// },
				// 
				// onEnd: evt => {
				// 	// const targetRow = this.list.splice(evt.oldIndex, 1)[0]
        //   // const tempIndex = this.newList.splice(evt.oldIndex, 1)[0]
        //   // this.list.splice(evt.newIndex, 0, targetRow)
        //   // this.newList.splice(evt.newIndex, 0, tempIndex)
				// },
			})

		}
	}
};
</script>
<style lang="less" scoped>
.drag-list {
	.title {
		display: inline-block;
		width: 50px;
		margin-right: 10px;
		text-align: right;
	}
	ul {
		li {
			height: 36px;	
			line-height: 36px;
			margin: 0;
			padding: 0 20px;
			border: 1px solid #999;
			border-bottom: none;
			cursor: pointer;
			&:last-child {
				border-bottom: 1px solid #999;
			}
			&.sortable-current, &.sortable-chosen {
				background-color: #fff6b2;
			}
		}
	}
	.lisg-group {
		display: flex;
		.list-box2, .list-box3 {
			flex: 1;
		}
		.list-box3 {
			margin-left: 100px;
		}
	}
}
</style>
